body, html {
  color: #212121;
  box-sizing: border-box;
  background: linear-gradient(#eceff1, #f5f5f5);
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

*, :before, :after {
  box-sizing: inherit;
}

.container {
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  padding-top: 96px;
  display: flex;
}

.app-bar {
  z-index: 100;
  width: 100%;
  box-shadow: none;
  background: none;
  align-items: center;
  padding: 16px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.toolbar {
  align-items: center;
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  display: flex;
}

#back-button {
  cursor: pointer;
  color: #424242;
  background: none;
  border: none;
  border-radius: 50%;
  margin-right: 16px;
  padding: 8px;
  transition: background .2s;
}

#back-button:hover {
  background: #0000000f;
}

.icon-button .material-icons {
  font-size: 28px;
}

.title {
  color: #212121;
  font-size: 1.5rem;
  font-weight: 600;
}

.notifications-list {
  flex-direction: column;
  gap: 20px;
  max-width: 960px;
  margin: 0 auto;
  padding: 0 16px 64px;
  list-style: none;
  display: flex;
}

.notification-card {
  background-color: #fff;
  border-radius: 16px;
  align-items: flex-start;
  padding: 20px;
  transition: transform .2s, box-shadow .2s;
  display: flex;
  box-shadow: 0 4px 12px #00000014;
}

.notification-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 16px #0000001f;
}

.notification-icon {
  color: #fff;
  border-radius: 50%;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  margin-right: 20px;
  font-size: 28px;
  display: flex;
}

.alert .notification-icon {
  background-color: #e53935;
}

.report .notification-icon {
  background-color: #43a047;
}

.notification-content {
  flex: 1;
}

.notification-title {
  color: #212121;
  margin: 0 0 6px;
  font-size: 1.2rem;
  font-weight: 600;
}

.notification-body {
  color: #555;
  margin: 0 0 8px;
  font-size: .95rem;
  line-height: 1.5;
}

.notification-time {
  color: #888;
  font-size: .85rem;
}

.notification-card.alert {
  border-left: 6px solid #e53935;
}

.notification-card.report {
  border-left: 6px solid #43a047;
}

.footer-spacing {
  height: 56px;
}
